export function ReadFile(url){
    const xhr = new XMLHttpRequest()
    const okStatus = document.location.protocol === 'file:' ? 0 : 200
    xhr.open('GET', url, false)
    xhr.overrideMimeType('text/html;charset=utf-8')// 默认为utf-8
    xhr.send(null)
    return xhr.status === okStatus ? xhr.responseText : ''
}
export function StrToBool(str){
    let b=false
    if (["开","open","对","是","1"].indexOf(str)!== -1){  //字符符合任意一项就执行
        b=true
    }
    else if (["关","close","错","否","0"].indexOf(str)!== -1){
        b=false
    }
    return b
}
export function GetSettingVolume(){
    if(localStorage.getItem('playVolumeValue'))
        return parseInt(localStorage.getItem('playVolumeValue'))/100
    else
        return 1
}
