<template>
  <div>
<!--    背景图-->
    <transition name="fade" @before-enter="imgBeforeEnter" @enter="imgEnter"  @after-enter="imgAfterEnter">
      <img :src="imgFile" v-if="imgShow" ref="img" alt="图片请加载失败，请检查文件名或请刷新重试~" :style="imgAnimCss" class="imgStyle"/>
    </transition>
    <!--    声音-->
    <audio ref="audioBGM" loop :src="audioFile"/>
    <!--    视频-->
    <transition name="fade">
      <div  style="width:100%; height:100%;padding-bottom:0;background-size:100% 100%" v-show="videoShow">
        <video :src='videoFile' ref="videoRef" autoplay style="" loop></video>
      </div>
    </transition>
<!--    Home组件 -->
    <Home v-show="HomeShow" :language="language" @startInterface="startInterface" @OpenSetting="OpenOrCloseSetting" @nextJson="nextJson"/>
    <Role v-show="RoleShow" @nextJson="nextJson"/>
    <TextBoard v-show="TextBoardShow" @nextJson="nextJson" @OpenSetting="OpenOrCloseSetting"/>
    <Choose v-show="ChooseShow" @nextJson="nextJson" :ChooseList="ChooseList" @ChooseClick="ChooseClick"/>
    <Soliloquy @nextJson="nextJson" />
    <transition  name="fade"  @before-enter="imgBeforeEnter" @enter="imgEnter"  @after-enter="imgAfterEnter" @leave="imgLeave" @before-leave="imgBeforeLeave">
      <Setting v-if="SettingShow" @CloseSetting="OpenOrCloseSetting"></Setting>
    </transition>
  </div>
</template>

<script>
import Home from "./Home"
import Role from "./Role"
import TextBoard from "./TextBoard";
import {GetSettingVolume, ReadFile, StrToBool} from "@/SystemFun";
import Choose from "@/components/Choose";
import Soliloquy from "@/components/Soliloquy";
import Setting from "@/components/Setting";
export default {
  name: "Background",
  data(){
    return{
      ImgUrl:'defaultPic.jpg',  //一定要设置默认图片
      VideoUrl:'',              //视频的url bh1.mp4
      AudioUrl:'',              //声音的 url bgm.mp3
      imgShow:false,            // 图片显示控制
      videoShow:false,          //视频显示控制
      HomeShow:false,       //是否显示主界面
      language:'CN',         //主界面语言
      canChangeSwitch:true,   //控制能不能 控制下一条开关.
      RoleShow:false,          //控制人物界面显示
      //存放动画的数组
      AnimateDataArr:[],
      // 预设的动画开始
      AnimBefore:{"opacity":0},
      // 预设的动画结束
      AnimAfter:{"opacity":1},
      // 预设的动画持续时间
      AnimDuration:500,
      AnimCssDuration:500,
      // 预设的动画循环次数
      AnimLoop:0,
      //文字面板显示
      TextBoardShow:false,
      IsCssAnim:false,
      imgAnimCss:"",
      ChooseShow:false,
      JsonKey:'',
      ChooseList:[],
      SettingShow:false
    }
  },
  mounted() {
    this.imgShow=true       //默认设置显示 图片
    // 获取动画设置
    this.getAnimationTxt()
    // 全局事件总线 绑定
    this.$bus.$on('changeImg',this.changeImg)
    this.$bus.$on('changeVideo',this.changeVideo)
    this.$bus.$on('changeBGM',this.changeAudio)
    this.$bus.$on('changeBGMSwitch',this.changeBGMSwitch)
    this.$bus.$on('changeTextSwitch',this.changeTextSwitch)
    this.$bus.$on('changeRoleSwitch',this.changeRoleSwitch)
    this.$bus.$on('startInterface',this.startInterface)
    this.$bus.$on('changeAnimate',this.changeAnimate)
    this.$bus.$on('changeAnimateCss',this.changeAnimateCss)
    this.$bus.$on('imgTrans',this.imgTrans)
    this.$bus.$on('choose',this.choose)
    this.$bus.$on('changeVideoPause',this.changeVideoPause)
    this.$bus.$on('setNewVolume',this.setNewVolume)
  },
  methods:{
    imgBeforeEnter(el){
      //动画开始前 设置透明度是0
      this.$store.state.clickNextSwitch=false
      if (this.IsCssAnim){
        return false
      }

      let KeyArr = Object.keys(this.AnimBefore);
      for (let i=0;i<KeyArr.length;i++){
        el.style[KeyArr[i]]=this.AnimBefore[KeyArr[i]]
      }

    },
    imgEnter(el,done){
      //动画进入时 在500ms内 透明度调整到1
      Velocity(el, this.AnimAfter, {
        // 完成时 调用done方法
        duration: this.AnimDuration,
        loop:this.AnimLoop,
        complete:done
      })
    },
    imgAfterEnter(){
      //进入动画完成
      if (!this.canChangeSwitch) return false       //如果是主界面，不能打开 控制下一步开关
      this.$store.state.clickNextSwitch=true
    },
    changeVideo(jsonKey, VideoUrl){
      if(VideoUrl===null||VideoUrl===''){
        this.$message.error('填写的视频路径是空的')
        return false;
      }
      this.$refs.videoRef.currentTime=0
      this.VideoUrl=''
      this.VideoUrl=VideoUrl; //修改视频url
      this.$nextTick(()=>{
          this.$refs.videoRef.volume=GetSettingVolume()
        this.$refs.videoRef.play() //谷歌浏览器 第一次播放视频必须单独调用，或者无声音播放（防扰民）
        this.videoShow=true;    //显示video
      })
      this.$store.commit('IsVideoShow',VideoUrl)
      this.JsonKey=jsonKey
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    imgLeave(el,done){
      Velocity(el, {
        opacity: 0
      }, {
        // 完成时 调用done方法
        duration: 300,
        complete:done
      })
    },
    imgBeforeLeave(el){
      el.style.opacity =1
    },
    nextJson(){
      this.$emit("nextJson")    //调用Main组件的切换
    },
    changeImg(jsonKey, imgName){
      if(imgName===null||imgName===''){
        this.$message.error('填写的图片路径是空的')
        return false;
      }
      this.videoShow=false
      this.$refs.videoRef.pause()
      this.imgShow=false
      this.ImgUrl=''
      this.$nextTick(()=>{
        this.ImgUrl=imgName
        this.imgShow=true})
      this.$store.commit('IsImageShow',imgName)
      this.$store.state.ImgTrans=''
      this.JsonKey=jsonKey
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },this.IsCssAnim?this.AnimCssDuration:this.AnimDuration)
      }
    },
    changeBGMSwitch(jsonKey, Switch){
      this.$refs.audioBGM.pause()
      if(this.AudioUrl===null||this.AudioUrl===''){
        return false;
      }
      this.$nextTick(()=>{
        if (StrToBool(Switch)){  //字符符合任意一项就执行
          this.$refs.audioBGM.load()
          this.$refs.audioBGM.play()
          this.$refs.audioBGM.volume=GetSettingVolume()
          this.$store.state.audioBGMUrlPlay=true
        }
        else if (!StrToBool(Switch)){
          this.$refs.audioBGM.pause()
          this.$store.state.audioBGMUrlPlay=false
        }
      })
      this.JsonKey=jsonKey
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    changeTextSwitch(jsonKey, Switch){
      this.$nextTick(()=>{
        let b=StrToBool(Switch)
        this.$nextTick(()=>{
          this.TextBoardShow=b
        })
        this.$store.state.TextBoardShow=b
      })
      this.JsonKey=jsonKey
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    changeRoleSwitch(jsonKey, Switch){
      //人物界面是否显示
      this.$nextTick(()=>{
        let b=StrToBool(Switch)
        this.$nextTick(()=>{
          this.RoleShow=b
        })
        this.$store.state.RoleShow=b
      })
      this.JsonKey=jsonKey
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    changeAudio(jsonKey, AudioUrl){
      // 更换背景音乐
      if(AudioUrl===null||AudioUrl===''){
        this.$message.error('填写的背景音乐路径是空的')
        return false;
      }
      this.AudioUrl=AudioUrl;
      this.$store.state.audioBGMUrl=AudioUrl
      if(jsonKey.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    startInterface(jsonKey, language){
      //显示关闭主页
      this.$nextTick(()=>{
        this.HomeShow=!this.HomeShow
        //关闭背景音乐 和视频的播放
        this.$bus.$emit("changeBGMSwitch",'','0')
        this.$bus.$emit("changeVideoPause",'','0')
        //如果是点击了继续按钮 重新播放视频 音乐
        if(jsonKey==='continue'){
          let a=this.$store.state.audioBGMUrlPlay
          this.$bus.$emit("changeBGMSwitch",'',a?'1':'0')
          let b=this.$store.state.videoShow
          this.$bus.$emit("changeVideoPause",'',b?'1':'0')
        }
        this.$bus.$emit("startPlay",this.HomeShow)
      })
      if (language==='EN'||language==='en')
        this.language=language
      this.canChangeSwitch=this.HomeShow      //主界面不能控制下一条
      this.$store.state.clickNextSwitch=this.canChangeSwitch

    },
    choose(jsonKey, jsonVal){
      //选择分支界面显示
      let arr=jsonVal.split('|')
      arr=arr.map(a=>JSON.parse(a.replaceAll("'",'"')))
      this.ChooseList=arr
      this.JsonKey=jsonKey
      this.$nextTick(()=>{
        this.ChooseShow=!this.ChooseShow
        //this.TextBoardShow=false
      })
    },
    ChooseClick(itemVal){
      //分支选择之后 判断是否载入文件 或者查找分支
      this.ChooseShow=false
      if(itemVal.indexOf(".txt") !== -1)
        this.$emit("loadAnotherFile",itemVal)
      else
        this.$emit("findChooseLabel",itemVal)
    },
    getAnimationTxt(){
      //获取用户的动画预设
      let fileData=ReadFile('static/SystemFile/AnimationSetting.txt')
      //去掉回车
      this.AnimateDataArr=fileData.split("\n");
      // 去掉空行
      this.AnimateDataArr=this.AnimateDataArr.filter(i=>i && i.trim())
      this.AnimateDataArr=this.AnimateDataArr.map(item=>JSON.parse('{'+item+'}'))
    },
    changeAnimate(key,value){
      //根据 定义的 "切换动画":"预设动画" 过滤
      let anim=this.AnimateDataArr.filter(item => item[value])
      if (anim.length===0) return false
      this.imgAnimCss=""
      this.$store.state.imgAnimCss=""
      let arr=anim[0][value].split('|')
      if (arr.length<2)return false
      this.AnimBefore=JSON.parse(arr[0].replaceAll("'",'"'))
      this.AnimAfter=JSON.parse(arr[1].replaceAll("'",'"'))
      this.$store.state.AnimBefore=this.AnimBefore
      this.$store.state.AnimAfter=this.AnimAfter
      console.log(this.AnimBefore);
      if (arr.length===3){
        this.AnimDuration=JSON.parse(arr[2].replaceAll("'",'"'))['持续时间']
        this.AnimLoop=JSON.parse(arr[2].replaceAll("'",'"'))['循环']
      }
      else{
        this.AnimDuration=500
        this.AnimLoop=0
      }
      this.$store.state.AnimDuration=this.AnimDuration
      this.$store.state.AnimLoop=this.AnimLoop
      this.IsCssAnim=false
      this.$store.state.IsCssAnim=false
      this.$store.state.ImgTrans=''
      this.JsonKey=key
      if(key.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    changeAnimateCss(key,value){
      //切换背景图片动画为css动画
      let arr=value.split('|')
      this.IsCssAnim=true
      this.AnimBefore={"opacity":0}
      this.AnimAfter={"opacity":1}
      this.$store.state.AnimBefore=this.AnimBefore
      this.$store.state.AnimAfter=this.AnimAfter
      this.imgAnimCss='animation:'+arr[0]
      this.$store.state.AnimCss=arr[0]
      this.$store.state.IsCssAnim=true
      this.$store.state.ImgTrans=''
      this.JsonKey=key
      this.AnimCssDuration=500
      if(arr.length===2){
        this.AnimCssDuration=arr[1]
      }
      if(key.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },10)
      }
    },
    imgTrans(key,value){
      //背景动画偏移
      let arr=value.split('|')
      this.$refs.img.style.transform=arr[0]
      this.$refs.img.style.transition='all 500ms ease'
      let AnimDuration=500
      if (arr.length===2){
        this.$refs.img.style.transition='all '+arr[1]+'ms ease'
        AnimDuration=arr[1]
      }
      this.$store.state.ImgTrans=value
      this.JsonKey=key
      if(key.indexOf("_n") !== -1){
        setTimeout(()=>{
          this.$bus.$emit("nextJson")
        },AnimDuration)
      }
    },
    changeVideoPause(jsonKey,jsonValue){
      //视频播放（显示）与暂停（隐藏）
      let b=StrToBool(jsonValue)
      if (b){
        this.videoShow=true
        this.$refs.videoRef.play()
      }else {
        this.videoShow=false
        this.$refs.videoRef.pause()
      }
    },
    OpenOrCloseSetting(){
      this.SettingShow=!this.SettingShow
    },
    setNewVolume(playVolumeValue){
      this.$refs.audioBGM.volume=playVolumeValue/100
      this.$refs.videoRef.volume=playVolumeValue/100
    }
  },
  computed:{
    imgFile(){      //计算属性 切换图片 下同
      if (this.ImgUrl!=='')
        return './static/BackgroundSource/'+this.ImgUrl
      else
        return false;
    },
    audioFile(){
      if (this.AudioUrl!=='')
        return './static/BGM/'+this.AudioUrl
      else
        return false;
    },
    videoFile(){
      if (this.VideoUrl!=='')
        return './static/BackgroundSource/'+this.VideoUrl
      else
        return false;
    },
    videoStyle(){   // 动态更改属性 修改视频父级div的宽高 实现 视频自适应窗口
      return 'width:100%; height:100%;padding-bottom:0;background-size:100% 100%';
    }
  },
  watch:{ //监察 改变是重新赋值 完事调用 videoStyle

  },
  beforeDestroy() {
    //全局事件 解绑
    this.$bus.$off('changeImg')
    this.$bus.$off('changeVideo')
    this.$bus.$off('changeBGM')
    this.$bus.$off('changeBGMSwitch')
    this.$bus.$off('startInterface')
    this.$bus.$off('changeAnimate')
    this.$bus.$off('changeAnimateCss')
    this.$bus.$off('imgTrans')
    this.$bus.$off('changeTextSwitch')
    this.$bus.$off('changeRoleSwitch')
    this.$bus.$off('choose')
    this.$bus.$off('changeVideoPause')
    this.$bus.$off('setNewVolume')
  },
  components:{Soliloquy, Choose, TextBoard, Home,Role,Setting}
}
</script>

<style scoped>
/*video的样式*/
video{
  position:absolute;
  left:0;
  top:0;
  width:100%;
  height:100%;
  object-fit: fill;
}
.imgStyle{
  z-index:-99;
  height: 100%;
  width: 100%;
  background-size: 100% 100%;
  position: fixed;
}
</style>
